'use client';
import { FaWhatsapp } from 'react-icons/fa';

type Props = {
  /** E.164 phone number WITHOUT “+” (e.g. 919876543210) */
  phone: string;
  /** Optional pre-filled text that appears in WhatsApp */
  message?: string;
};

export default function FloatingWhatsapp({ phone, message = '' }: Props) {
  const url = `https://wa.me/${phone}${message ? `?text=${encodeURIComponent(message)}` : ''}`;

  return (
    <a
      href={url}
      target="_blank"
      rel="noopener noreferrer"
      aria-label="Chat with us on WhatsApp"
      className="
        fixed z-50 bottom-4 right-4 flex h-14 w-14 items-center
        justify-center rounded-full bg-green-500 shadow-lg
        transition-transform hover:scale-110 focus:outline-none
      "
    >
      <FaWhatsapp className="h-7 w-7 text-white" />
      <span className="sr-only">WhatsApp</span>
    </a>
  );
}